.main--about {
  height: 100vh;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #fff;
}
.header--about {
  font-size: 50px;
  font-weight: bold;
}
.p--about {
  margin-top: 20px;
  font-size: 25px;
  max-width: 70%;
  font-weight: lighter;
  text-align: left;
}
.cta--about {
  margin-top: 30px;
  color: #fff;
  font-size: 40px;
  background-color: #f58634;
  border-style: none;
  border-radius: 15px;
  padding: 15px 15px;
  cursor: pointer;
  width: 349px;
  height: 81px;
  margin-bottom: 30px;
}
@media screen and (max-width: 1280px) {
  .main--about {
    margin-left: 10px;
    margin-right: 10px;
  }
}
@media screen and (max-width: 600px) {
  .header--about {
    font-size: 40px;
  }
  .p--about {
    font-size: 20px;
    max-width: 90%;
  }
}
@media screen and (max-width: 500px) {
  .main--about {
    height: 80vh;
  }
  .header--about {
    font-size: 30px;
  }
  .p--about {
    font-size: 15px;
    max-width: 90%;
  }
  .cta--about {
    margin-top: 15px;

    font-size: 30px;

    cursor: pointer;
    width: 300px;
    height: 61px;
    align-items: center;
    display: flex;
    justify-content: center;
    text-align: center;
  }
}
