@import url("https://fonts.googleapis.com/css2?family=Comfortaa&display=swap");
.form-main--RoomCreate {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
  flex-direction: column;
}
.inputs-row--RoomCreate {
  color: #fff;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.inputs-blurb--RoomCreate {
  text-align: center;
  font-size: 40px;
}
.inputs-blurb--RoomCreate input {
  margin-top: 10px;
  width: 188px;
  height: 57px;
  border-radius: 25px;
  border-style: none;
  outline: none;
}
.inputs-blurb--RoomCreate input::placeholder {
  margin-left: 25px;
  font-size: 20px;
  text-align: center;
  color: gray;
  font-family: "Comfortaa", cursive;
}
.inputs-blurb-RoomCreate input::-moz-placeholder {
  font-size: 20px;
  text-align: center;
  color: gray;
  margin-left: 25px;
  font-family: "Comfortaa", cursive;
}
.inputs-blurb--RoomCreate input:focus::placeholder {
  color: transparent;
}
.inputs-blurb--RoomCreate input[type="text"] {
  text-align: center;
  font-size: 15px;
}
.submit-button--RoomCreate {
  background-color: #81b214;
  font-size: 40px;
  border-style: none;
  border-radius: 15px;
  color: #fff;
  width: 300px;
  margin-top: 15px;
  cursor: pointer;
  height: 80px;
  margin-right: 15px;
  margin-bottom: 15px;
}
@media screen and (max-width: 1000px) {
  .inputs-blurb--RoomCreate {
    font-size: 30px;
  }
}
@media screen and (max-width: 748px) {
  .inputs-blurb--RoomCreate {
    font-size: 30px;
  }
  .inputs-row--RoomCreate {
    text-align: left;
    flex-direction: column;
    height: 50vh;
  }
}
@media screen and (max-width: 748px) {
  .inputs-blurb--RoomCreate {
    font-size: 25px;
  }
  .submit-button--RoomCreate {
    font-size: 27px;

    width: 250px;

    height: 65px;
  }
}
