.container--landing {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}
.section1--landing {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.section1-textArea--landing svg {
  min-width: 521.66px;
  min-height: 455px;
}
.section1-textArea--landing {
  color: #fff;
  width: 80%;
}
.section1-textArea--landing h1 {
  font-size: 60px;
}
.section1-textArea--landing p {
  margin-top: 30px;
  font-size: 30px;
  font-weight: lighter;
  width: 70%;
}
.section1-image--landing {
  width: 547.94px;
  height: 461px;
}
/* CTA BUTTON */
/* `````````` */
/* ````````` */
.cta-section1--landing {
  margin-top: 30px;
  color: #fff;
  font-size: 40px;
  background-color: #f58634;
  border-style: none;
  border-radius: 15px;
  padding: 15px 15px;
  max-width: 349px;
  height: 81px;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  text-align: right;
}
/* `````````` */
/* ````````` */
.section2-bgcolor--Landing {
  background-color: rgb(255, 255, 255);
  height: 100vh;
  width: 100%;
}

.section2--landing {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
.section2--landing h2 {
  font-size: 50px;
  text-align: center;
  padding-top: 15px;
}
.section2--landing p {
  margin-top: 30px;
  font-size: 30px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-weight: lighter;
  text-align: center;
}
.blurb-row--landing {
  margin-top: 20px;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.blurb--landing {
  max-width: 269px;
  margin-left: auto;
  margin-right: auto;
}
.blurb-row--landing h3 {
  text-align: center;
  font-size: 40px;
  font-weight: normal;
}
.blurb--landing p {
  font-size: 25px;
  font-weight: lighter;
  height: 112px;
}
.blurb-row--landing svg {
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
}
.cta-section2--landing {
  margin-top: 30px;
  margin-bottom: 15px;
  color: #fff;
  font-size: 40px;
  background-color: #f58634;
  border-style: none;
  border-radius: 15px;
  padding: 15px 15px;
  cursor: pointer;
  width: 349px;
  height: 81px;
  margin-left: auto;
  margin-right: auto;
}
/* Section 3*/
.section3--landing {
  width: 100%;
  height: 900px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.section3-bgcolor--Landing {
  background-color: #81b214;
  width: 100%;
  color: #ffff;
  height: 950px;
}
.section3--landing h2 {
  font-size: 50px;
  margin-top: 20px;
  text-align: center;
}
.section3--landing p {
  font-size: 30px;
  text-align: center;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}
.section3-explanation--landing {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.section3-explain-row--landing {
  width: 400px;
}
.section3-explain-blurb--landing {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}
.section3-explain-row--landing svg {
  min-width: 82px;
  float: right;
}
.section3-line--Landing {
  width: 90%;
  height: 1px;
  margin-left: auto;
  margin-right: auto;
  background-color: gray;
  margin-top: 10px;
}
.cta-section3--landing {
  margin-top: 30px;
  color: #fff;
  font-size: 40px;
  background-color: #f58634;
  border-style: none;
  border-radius: 15px;
  padding: 15px 15px;
  cursor: pointer;
  width: 349px;
  height: 81px;
  margin-bottom: 30px;
}
/* Section 4 */
.section4--landing {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  height: 500px;
  text-align: center;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: #fff;
  flex-direction: column;
}
.section4--landing img {
  width: 416px;
  height: 166px;
}
.section4--landing p {
  margin-top: 25px;
}
.cta-section4--landing {
  margin-top: 25px;
  color: #fff;
  font-size: 40px;
  background-color: #ffcc29;
  border-style: none;
  border-radius: 15px;
  padding: 15px 15px;
  cursor: pointer;
  width: 349px;
  height: 81px;
  margin-top: 50px;
}
@media screen and (max-width: 1280px) {
  .section1--landing {
    margin-left: 10px;
    margin-right: 10px;
  }
  .section3--landing {
    margin-left: 10px;
    margin-right: 10px;
  }
}
@media screen and (max-width: 850px) {
  .cta-section1--landing {
    font-size: 30px;
  }
  .section1--landing svg {
    width: 375px;
    height: 300px;
  }
  /* Section 222222222*/
  .section2--landing p {
    font-size: 27px;
    width: 80%;
  }
  .blurb--landing svg {
    display: block;
    width: 200px;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
  }
  .blurb--landing p {
    display: block;
    width: 200px;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
  }
  /*section 33333333333333333333333333
  33333
  333
  333
  */

  .section3--landing h2 {
    font-size: 40px;
  }
  .section3--landing p {
    font-size: 25px;
  }
  .section3--landing {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  .section3-explanation--landing svg {
    display: none;
  }
  .section3-explanation--landing {
    height: 100vh;
    display: flex;
    margin-top: 20px;
    align-items: center;
  }
  .section3-explain-row--landing svg {
    display: flex;
  }
  .section3-explain-row--landing {
    margin-left: auto;
    margin-right: auto;
  }
