    margin-right: auto;
    width: 349px;
    height: 81px;
  }
}
@media screen and (max-width: 650px) {
  .cta-section1--landing {
    font-size: 30px;
    width: 300px;
    height: 61px;
    padding: 0;
    text-align: center;
  }
  .section1--landing h1 {
    font-size: 50px;
  }
  .section1--landing p {
    font-size: 25px;
  }
  .section1--landing svg {
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .section1--landing {
    justify-content: space-around;
    align-items: flex-start;
    height: 100vh;
    flex-direction: column;
  }
  .section1-textArea--landing {
    margin-top: 50px;
  }
  .section2--landing {
