.main--SignIn {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #81b214;
}
.button--SignIn {
  width: 349px;
  height: 81px;
  border-style: none;
  padding: 5px 15px;
  border-radius: 15px;
  cursor: pointer;
  color: #fff;
  font-size: 40px;
  background-color: #81b214;
}
@media screen and (max-width: 802px) {
  .cta-button {
    font-size: 30px;
    width: 253px;
  }
}
@media screen and (max-width: 702px) {
  .cta-button {
    font-size: 20px;
    width: 141px;
  }
}
