.main--messages {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  color: #fff;
  flex-direction: column;
}
.main--messages h2 {
  font-size: 50px;
}
.section-messages--Messages {
  background-color: #fff;
  width: 80%;
  margin-top: 20px;
  border-radius: 20px;
  height: 100vh;
  overflow: auto;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column-reverse;
}
.wrapper--messages {
  width: 100%;
}
.msg--messages {
  max-width: 200px;
  height: auto;
  word-break: break-all;
  display: block;
  align-items: center;
  justify-content: center;
  padding: 25px 50px;
  text-align: center;
  margin: 20px 0;
  border-radius: 25px;
  font-size: 20px;
  margin-left: auto;
  margin-right: auto;
  color: white;
}
.sent--messages {
  background-color: #ffcc29;

  float: right;
  margin-right: 20px;
}
.recevied--messages {
  background-color: #f8a467;
  float: left;
  margin-left: 20px;
}
.form--messages {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.input--messages {
  width: 70%;
  outline: none;
  line-height: 1;
  height: 63px;
  border-radius: 20px 0px 0px 20px;
  border-style: none;
  resize: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.send-button--messages {
  width: 10%;
  height: 63px;
  color: white;
  font-size: 30px;
  font-weight: lighter;
  border-style: none;
  border-radius: 0px 20px 20px 0px;
  background-color: #81b214;
}
.input--messages[name="text-area"] {
  font-size: 20px;
  word-break: break-all;
  font-family: "Comfortaa", cursive;
  padding-left: 5px;
  display: flex;
}
@media screen and (max-width: 720px) {
  .send-button--messages {
    font-size: 20px;
    width: 15%;
  }
  .input--messages {
    width: 65%;
  }
}
@media screen and (max-width: 475px) {
  .msg--messages {
    max-width: 100px;
    font-size: 15px;
  }
}
@media screen and (max-width: 300px) {
  .msg--messages {
    max-width: 75px;
    font-size: 12px;
  }
}
@media screen and (max-width: 350px) {
  .send-button--messages {
    font-size: 15px;
    width: 20%;
  }
  .input--messages {
    width: 60%;
  }
  .input--messages[type="text"] {
    font-size: 15px;
  }
}
