.header--nav {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}
.navigation--nav {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-top: 50px;
  width: 100%;
  height: 100px;
}
.menu--nav {
  width: 100px;
  height: 60px;
  display: none;
}
.logo--nav {
  width: 250px;
  height: 100px;
}
.list-ul--nav {
  width: 100%;
  display: inline-flex;
  height: 100px;
  justify-content: flex-end;
  list-style: none;
  align-items: center;
}
.list-ul--nav li {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 30px;
  padding-right: 25px;
}
.cta--nav {
  background-color: #81b214;
  padding: 10px 25px 10px 25px;
  text-align: center;
  border-radius: 25px;
}
@media screen and (max-width: 1280px) {
  .header--nav {
    margin-left: 10px;
    margin-right: 10px;
  }
}
@media screen and (max-width: 770px) {
  .logo--nav {
    width: 170px;
    height: 70px;
  }
  .list-ul--nav li {
    font-size: 20px;
  }
  .cta--nav {
    font-size: 20px;
  }
}
@media screen and (max-width: 550px) {
  .logo--nav {
    width: 128px;
    height: 51px;
  }
  .menu--nav {
    display: flex;
  }
  .off--nav {
    display: none;
  }
  .navigation--nav {
    margin-top: 5px;
  }
  .cta--nav {
    font-size: 20px;
    padding: 0;
    background-color: unset;
    text-align: left;
  }
  .list-ul--nav {
    transform: translateX(100%);
  }
  .on--nav {
    background-color: tomato;
    position: absolute;
    width: 100%;
    right: 0;
    top: 10vh;
    height: 7vh;

    justify-content: center;
    align-items: center;
    transform: translateX(0%);
    transition: transform 0.5s ease-out;
  }
  .on--nav li {
    width: 100%;
    font-size: 15px;
  }
}
