.main--Rooms {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.room-section--Rooms {
  margin-top: 30px;
  border-radius: 35px;
  background-color: #fff;
  width: 80%;
  height: 100vh;
  overflow: auto;
}
.room-rows--Rooms {
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 50px;
}
.line--Rooms {
  margin-top: 5px;
  width: 300px;
  color: black;
  height: 5px;
}
.room-stats--Rooms {
  margin: 10px 15px;
  max-width: 200px;
}
.room-stats--Rooms h1 {
  font-size: 40px;
  max-width: 200px;
  display: block;
}
.text-length--Rooms {
  max-width: 10px;
}
.room-stats--Rooms h2 {
  font-size: 30px;
  max-width: 300px;
}
.join-button--Rooms {
  background-color: #81b214;
  font-size: 40px;
  border-style: none;
  border-radius: 15px;
  color: #fff;
  width: 300px;
  margin-top: 15px;
  cursor: pointer;
  height: 60px;
  margin-right: 15px;
}
.create-room--Rooms {
  background-color: #f58634;
  font-size: 40px;
  border-style: none;
  border-radius: 15px;
  color: #fff;
  width: 334px;
  margin-top: 15px;
  cursor: pointer;
  height: 81px;
  margin-bottom: 15px;
}
@media screen and (max-width: 720px) {
  .room-stats--Rooms h1 {
    font-size: 30px;
  }
  .room-section--Rooms h2 {
    font-size: 20px;
    max-width: 200px;
  }
  .line--Rooms {
    width: 200px;
  }
  .join-button--Rooms {
    font-size: 30px;

    width: 250px;

    height: 50px;
  }
}
@media screen and (max-width: 462px) {
  .room-stats--Rooms h1 {
    font-size: 25px;
  }
  .room-section--Rooms h2 {
    font-size: 15px;
    max-width: 150px;
  }
  .line--Rooms {
    width: 150px;
  }
  .join-button--Rooms {
    font-size: 25px;

    width: 200px;

    height: 40px;
  }
  .create-room--Rooms {
    font-size: 25px;

    width: 250px;

    height: 55px;
  }
}
@media screen and (max-width: 345px) {
  .line--Rooms {
    width: 120px;
  }
  .room-section--Rooms h2 {
    max-width: 120 px;
  }
}
