@import url("https://fonts.googleapis.com/css2?family=Comfortaa&display=swap");

.page--contact-contact {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mailinfo--text-contact {
  margin-top: 200px;
  display: inline-flex;
  align-items: center;
}
.mail--text-contact {
  font-size: 20px;
  color: #fff;
  margin-left: 20px;
}
.icon--mail-contact {
  width: 100px;
  height: 100px;
  color: #81b214;
}

.message--form-contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 521px;
  height: 600px;
  background-color: #fff;
  border-radius: 35px;
  float: right;
  color: rgb(0, 0, 0) !important;
}
.input--text-contact {
  margin-top: 22px;
  font-size: 20px;
}
.input--text-contact input {
  font-size: 20px;
  border-color: #81b214;
  border-style: solid;
  border-radius: 5px;
  width: 451px;
  height: 71px;
  outline: none;
}
.input--text-contact textarea {
  font-size: 20px;
  border-color: #81b214;
  border-style: solid;
  border-radius: 5px;
  width: 451px;
  height: 140px;
  font-family: "Comfortaa", cursive;
  outline: none;
}
.input--text-contact textarea ::placeholder {
  margin-left: 500px;
  font-family: "Comfortaa", cursive;
}
.input--text-contact textarea ::-moz-placeholder {
  margin-left: 500px;
}
.input--text-contact p {
  margin-bottom: 8px;
}
.input--text-contact input::placeholder {
  color: rgb(0, 0, 0);
  opacity: 10%;
  font-size: 20px;
  line-height: 0px;
  margin-left: 150px;
}
.input--text-contact input::-moz-placeholder {
  color: rgb(0, 0, 0);
  opacity: 10%;
  font-size: 20px;
  line-height: 0px;
  margin-left: 150px;
}

.button--send-contact {
  width: 204px;
  height: 50px;
  background-color: #81b214;
  padding: 5px 5px;
  border-radius: 15px;
  font-size: 20px;
  margin-top: 25px;
  border-style: none;
  color: #fff;
}
.heading--text-contact {
  font-size: 60px;
  color: #fff;
}
@media screen and (max-width: 925px) {
  .page--contact-contact {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .mailinfo--text-contact {
    margin: 0px;
  }
}
@media screen and (max-width: 535px) {
  .heading--text-contact {
    font-size: 50px;
    text-align: center;
  }
  .message--form-contact {
    width: 80%;
    height: 520px;
  }

  .input--text-contact {
    width: 80%;
  }
  .input--text-contact input {
    width: 100%;
  }
  .input--text-contact textarea {
    width: 100%;
  }
  .button--send-contact {
    width: 169px;
    height: 40px;
    background-color: #81b214;
    padding: 5px 5px;
    border-radius: 15px;
    font-size: 20px;
    margin-top: 2px;
  }
}
@media screen and (max-width: 375px) {
  .heading--text-contact {
    font-size: 40px;
    text-align: center;
    margin-top: 0;
  }
  .message--form-contact {
    width: 80%;
    height: 520px;
  }

  .input--text-contact {
    width: 80%;
  }
  .input--text-contact input {
    width: 100%;
  }
  .input--text-contact textarea {
    width: 100%;
  }
  .button--send-contact {
    width: 169px;
    height: 40px;
    background-color: #81b214;
    padding: 5px 5px;
    border-radius: 15px;
    font-size: 20px;
    margin-top: 2px;
  }
  .icon--mail-contact {
    width: 45px;
    height: 35px;
  }
  .button--send-contact {
    width: 130px;
    height: 30px;
  }
  .contact--info-contact {
    margin-top: 0px;
  }
  .mailinfo--text-contact {
    font-size: 15px;
  }
}
